body {
  margin: 0;
}

li {
  list-style: none;
}

h2, p, ul {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.team {
  background-color: #ff6b08;
  margin-top: 400px;
  margin-bottom: 200px;
}

.team-card {
  width: 500px;
  height: 550px;
  scroll-snap-type: x mandatory;
  background-color: #fff;
  border-radius: 8px;
  gap: 12px;
  margin-left: auto;
  margin-right: auto;
  padding: 24px;
  scroll-padding: 24px;
  display: flex;
  overflow-x: scroll;
}

.team-title {
  text-align: center;
}

.team-box {
  width: 350px;
  padding-left: 75px;
}

.team-text {
  scroll-snap-align: start;
  border-radius: 8px;
  flex: 0 0 100%;
  padding-left: 0;
  padding-right: 24px;
}

.students-card {
  margin-top: 200px;
}

.card {
  width: 350px;
  height: 612px;
  transform-style: preserve-3d;
  cursor: pointer;
  background-color: #fff;
  border-radius: 25px;
  transition: transform .8s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 4px rgba(223, 0, 255, .25);
}

.card:hover {
  transform: translate(-50%, -50%)rotateY(180deg);
}

.text-back {
  box-sizing: border-box;
  width: 100%;
  height: 50%;
  background: #131315;
  border: 1px solid #000;
  border-radius: 0 0 25px 25px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.text-title {
  color: #fff;
  border-bottom: 1px solid #df08ff;
  padding-bottom: 15px;
  font-weight: 400;
}

.text-back-box {
  widows: 85%;
  margin: auto;
}

.text-back-text {
  color: #fff;
  margin-top: 5%;
  margin-bottom: 20%;
  font-weight: 400;
}

.text-back-span {
  color: #df08ff;
}

.image-container {
  width: 100%;
  height: 50%;
  border-radius: 25px 25px 0 0;
}

.flip-card-back {
  width: 100%;
  height: 100%;
  visibility: hidden;
  border-radius: 25px;
  position: absolute;
}

.flip-card-back:hover {
  color: #fff;
  visibility: visible;
  background-color: #19191a;
  transform: rotateY(180deg);
}

.flip-card-back-title {
  color: #fff;
  width: 100%;
  text-align: center;
  transform: translate(-50% -50%);
  position: absolute;
  top: 50%;
  left: 50%;
}

.flip-card-back-title:hover {
  transform: translate(-50%, -50%);
}

